import React, { PureComponent } from 'react'
import moment from 'moment'
import { init, dispose } from 'klinecharts'

import SettingModal from './settingmodal/SettingModal'
import { intl, setLanguage } from './intl/intl'

import './app.less'

const periods = [
  'Line', '1m', '5m', '15m', '30m',
  '1H', '2H', '4H', '6H', '12H',
  '1D', '1W', '1M', '1Y'
]

const graphicMarks = [
  { key: 'horizontalStraightLine', icon: 'icon-horizontal-straight-line' },
  { key: 'horizontalRayLine', icon: 'icon-horizontal-ray-line' },
  { key: 'horizontalSegmentLine', icon: 'icon-horizontal-segment-line' },
  { key: 'priceLine', icon: 'icon-price-line' },
  { key: 'verticalStraightLine', icon: 'icon-vertical-straight-line' },
  { key: 'verticalRayLine', icon: 'icon-vertical-ray-line' },
  { key: 'verticalSegmentLine', icon: 'icon-vertical-segment-line' },
  { key: 'straightLine', icon: 'icon-straight-line' },
  { key: 'rayLine', icon: 'icon-ray-line' },
  { key: 'segmentLine', icon: 'icon-segment-line' },
  { key: 'parallelStraightLine', icon: 'icon-parallel-straight-line' },
  { key: 'priceChannelLine', icon: 'icon-price-channel-line' },
  { key: 'fibonacciLine', icon: 'icon-fibonacci-line' }
]

const candleStickStyles = [
  { key: 'solid', text: 'all_solid', icon: 'icon-candle-stick-solid' },
  { key: 'stroke', text: 'all_stroke', icon: 'icon-candle-stick-stroke' },
  { key: 'up_stroke', text: 'up_stroke', icon: 'icon-candle-stick-up-stroke' },
  { key: 'down_stroke', text: 'down_stroke', icon: 'icon-candle-stick-down-stroke' },
  { key: 'ohlc', text: 'ohlc', icon: 'icon-candle-stick-ohlc' }
]

const technicalIndicatorTypes = [
  'MA', 'EMA', 'VOL', 'MACD', 'BOLL', 'KDJ',
  'RSI', 'BIAS', 'BRAR', 'CCI', 'DMI',
  'CR', 'PSY', 'DMA', 'TRIX', 'OBV',
  'VR', 'WR', 'MTM', 'EMV', 'SAR'
]

const textColorDark = '#D9D9D9'
const gridColorDark = '#393939'
const axisLineColorDark = '#888888'

const textColorLight = '#7F7F7F'
const gridColorLight = '#EEEEEE'
const axisLineColorLight = '#999999'

function generatedTimestamp (period, nowTimestamp) {
  if (nowTimestamp) {
    switch (period) {
      case 'Line':
      case '1m': {
        return moment(nowTimestamp).subtract(1, 'minutes').valueOf()
      }
      case '5m': {
        return moment(nowTimestamp).subtract(5, 'minutes').valueOf()
      }
      case '15m': {
        return moment(nowTimestamp).subtract(15, 'minutes').valueOf()
      }
      case '30m': {
        return moment(nowTimestamp).subtract(30, 'minutes').valueOf()
      }
      case '1H': {
        return moment(nowTimestamp).subtract(1, 'hours').valueOf()
      }
      case '2H': {
        return moment(nowTimestamp).subtract(2, 'hours').valueOf()
      }
      case '4H': {
        return moment(nowTimestamp).subtract(4, 'hours').valueOf()
      }
      case '6H': {
        return moment(nowTimestamp).subtract(6, 'hours').valueOf()
      }
      case '12H': {
        return moment(nowTimestamp).subtract(12, 'hours').valueOf()
      }
      case '1D': {
        return moment(nowTimestamp).subtract(1, 'days').valueOf()
      }
      case '1W': {
        return moment(nowTimestamp).subtract(1, 'weeks').valueOf()
      }
      case '1M': {
        return moment(nowTimestamp).subtract(1, 'months').valueOf()
      }
      case '1Y': {
        return moment(nowTimestamp).subtract(1, 'years').valueOf()
      }
    }
  } else {
    const nowTime = moment()
    switch (period) {
      case 'Line':
      case '1m': {
        return nowTime.startOf('minute').valueOf()
      }
      case '5m': {
        const dif = nowTime.valueOf() % (5 * 60 * 1000)
        return nowTime.valueOf() - dif
      }
      case '15m': {
        const dif = nowTime.valueOf() % (15 * 60 * 1000)
        return nowTime.valueOf() - dif
      }
      case '30m': {
        const dif = nowTime.valueOf() % (30 * 60 * 1000)
        return nowTime.valueOf() - dif
      }
      case '1H': {
        return nowTime.startOf('hour').valueOf()
      }
      case '2H': {
        const dif = nowTime.valueOf() % (2 * 60 * 60 * 1000)
        return nowTime.valueOf() - dif
      }
      case '4H': {
        const dif = nowTime.valueOf() % (4 * 60 * 60 * 1000)
        return nowTime.valueOf() - dif
      }
      case '6H': {
        const dif = nowTime.valueOf() % (6 * 60 * 60 * 1000)
        return nowTime.valueOf() - dif
      }
      case '12H': {
        const dif = nowTime.valueOf() % (12 * 60 * 60 * 1000)
        return nowTime.valueOf() - dif
      }
      case '1D': {
        return nowTime.startOf('day')
      }
      case '1W': {
        return nowTime.startOf('week')
      }
      case '1M': {
        return nowTime.startOf('month')
      }
      case '1Y': {
        return nowTime.startOf('year').valueOf()
      }
    }
  }
}

function generatedKLineDataList (period, nowTimestamp, basePrice = 5000, dataSize = 800) {
  const dataList = []
  let timestamp = nowTimestamp
  let baseValue = basePrice
  const prices = []
  for (let i = 0; i < dataSize; i++) {
    baseValue = baseValue + Math.random() * 20 - 10
    for (let j = 0; j < 4; j++) {
      prices[j] = (Math.random() - 0.5) * 12 + baseValue
    }
    prices.sort()
    const openIdx = +Math.round(Math.random() * 3).toFixed(0)
    let closeIdx = +Math.round(Math.random() * 2).toFixed(0)
    if (closeIdx === openIdx) {
      closeIdx++
    }
    const volume = Math.random() * 200 + 10
    timestamp = generatedTimestamp(period, timestamp)
    const kLineModel = {
      open: prices[openIdx],
      low: prices[0],
      high: prices[3],
      close: prices[closeIdx],
      volume: volume,
      timestamp
    }
    kLineModel.turnover = (kLineModel.open + kLineModel.close + kLineModel.high + kLineModel.low) / 4 * volume
    dataList.unshift(kLineModel)
  }
  return dataList
}

export default class App extends PureComponent {
  constructor (props) {
    super(props)
    this.onResize = () => {
      if (this.kLineChart) {
        this.kLineChart.resize()
      }
    }
    this.state = {
      theme: 'dark',
      language: 'zh-CN',
      currentPeriod: '1m',
      currentCandleStickIcon: 'icon-candle-stick-solid',
      currentCandleStickTechnicalIndicatorType: 'MA',
      currentTechnicalIndicatorType: 'MACD',
      isShowTechnicalIndicator: false,
      isShowSettingModal: false,
      timezone: { key: 'Asia/Shanghai', text: 'shanghai', utc: '(UTC+8)' },
      tempTimezone: { key: 'Asia/Shanghai', text: 'shanghai', utc: '(UTC+8)' },
      technicalIndicatorParams: {},
      tempTechnicalIndicatorParams: {}
    }
  }

  getChartStyle (theme, language) {
    const textColor = theme === 'dark' ? textColorDark : textColorLight
    const gridColor = theme === 'dark' ? gridColorDark : gridColorLight
    const axisLineColor = theme === 'dark' ? axisLineColorDark : axisLineColorLight
    return {
      grid: {
        horizontal: {
          display: true,
          color: gridColor
        },
        vertical: {
          display: true,
          color: gridColor
        }
      },
      candleStick: {
        priceMark: {
          high: {
            color: textColor
          },
          low: {
            color: textColor
          }
        }
      },
      technicalIndicator: {
        line: {
          colors: [textColor, '#F5A623', '#F601FF', '#1587DD', '#1e88e5']
        },
        lastValueMark: {
          display: true
        }
      },
      xAxis: {
        axisLine: {
          color: axisLineColor
        },
        tickLine: {
          color: axisLineColor
        },
        tickText: {
          color: textColor
        }
      },
      yAxis: {
        axisLine: {
          color: axisLineColor
        },
        tickLine: {
          color: axisLineColor
        },
        tickText: {
          color: textColor
        }
      },
      separator: {
        color: axisLineColor
      },
      floatLayer: {
        crossHair: {
          horizontal: {
            line: {
              color: axisLineColor
            }
          },
          vertical: {
            line: {
              color: axisLineColor
            }
          }
        },
        prompt: {
          candleStick: {
            labels: language === 'zh-CN' ? ['', '', '', '', '', ''] : ['T', 'O', 'C', 'H', 'L', 'V'],
            text: {
              color: textColor
            }
          },
          technicalIndicator: {
            text: {
              color: textColor
            }
          }
        }
      }
    }
  }

  componentDidMount () {
    const { theme, language } = this.state
    this.kLineChart = init('k-line-chart')
    window.addEventListener('resize', this.onResize)
    this.kLineChart.loadMore(timestamp => {
      this.applyMoreData(timestamp)
    })
    this.kLineChart.setStyleOptions(this.getChartStyle(theme, language))
    this.kLineChart.addTechnicalIndicator('VOL', 120)
    this.applyNewData(this.state.currentPeriod)
    this.setState({
      tempTechnicalIndicatorParams: this.kLineChart.getTechnicalIndicatorParamOptions(),
      technicalIndicatorParams: this.kLineChart.getTechnicalIndicatorParamOptions()
    })
  }

  componentDidUpdate (prevProps, prevState, snapshot) {
    const { theme, language } = this.state
    if (theme !== prevState.theme || language !== prevState.language) {
      this.kLineChart.setStyleOptions(this.getChartStyle(theme, language))
    }
  }

  componentWillUnmount () {
    window.removeEventListener('resize', this.onResize)
    if (this.loadMoreDataTimer) {
      clearTimeout(this.loadMoreDataTimer)
    }
    this.clearUpdateDataTimer()
    dispose('k-line-chart')
  }

  applyNewData (period) {
    this.clearUpdateDataTimer()
    this.kLineChart.applyNewData(generatedKLineDataList(period), true)
    this.updateData(period)
  }

  applyMoreData (timestamp) {
    this.loadMoreDataTimer = setTimeout(() => {
      const hisKLineDataList = this.kLineChart.getDataList()
      const firstKLineData = hisKLineDataList[0]
      this.kLineChart.applyMoreData(
        generatedKLineDataList(
          this.state.currentPeriod, timestamp, firstKLineData.close, 200
        ), true
      )
    }, 3000)
  }

  updateData (period) {
    this.updateDataTimer = setTimeout(() => {
      const hisKLineDataList = this.kLineChart.getDataList()
      const lastKLineData = hisKLineDataList[hisKLineDataList.length - 1]
      const lastKLineDataTimestamp = lastKLineData.timestamp
      const nowTimestamp = generatedTimestamp(period)
      const newKLineData = { ...lastKLineData }
      if (moment(lastKLineDataTimestamp).isBefore(nowTimestamp)) {
        newKLineData.high = newKLineData.close
        newKLineData.low = newKLineData.close
        newKLineData.open = newKLineData.close
        newKLineData.volume = 0
        newKLineData.turnover = 0
        newKLineData.timestamp = nowTimestamp
      } else {
        const price = (Math.random() - 0.5) * 10 + newKLineData.close
        const volume = +((Math.random() * 3).toFixed(0))
        newKLineData.close = price
        newKLineData.high = Math.max(newKLineData.high, price)
        newKLineData.low = Math.min(newKLineData.low, price)
        newKLineData.volume = newKLineData.volume + volume
        newKLineData.turnover = newKLineData.turnover + volume * price
      }
      this.kLineChart.updateData(newKLineData)
      this.updateData(period)
    }, Math.random() * 1000 + 200)
  }

  clearUpdateDataTimer () {
    if (this.updateDataTimer) {
      clearTimeout(this.updateDataTimer)
    }
  }

  changePeriod = (period) => () => {
    const { currentPeriod } = this.state
    if (currentPeriod !== period) {
      if (period === 'Line') {
        this.kLineChart.setCandleStickChartType('real_time')
      } else if (currentPeriod === 'Line') {
        this.kLineChart.setCandleStickChartType('candle_stick')
      }
      this.applyNewData(period)
      this.setState({
        currentPeriod: period
      })
    }
  }

  changeCandleStickStyle = (candleStick) => () => {
    this.kLineChart.setStyleOptions({ candleStick: { bar: { style: candleStick.key } } })
    this.setState({
      currentCandleStickIcon: candleStick.icon
    })
  }

  changeTechnicalIndicatorType = (technicalIndicatorType) => () => {
    if (this.state.currentTechnicalIndicatorType !== technicalIndicatorType) {
      this.kLineChart.setTechnicalIndicatorType(this.technicalIndicatorTag, technicalIndicatorType)
      this.setState({
        currentTechnicalIndicatorType: technicalIndicatorType
      })
    }
  }

  changeCandleStickTechnicalIndicatorType = (technicalIndicatorType) => () => {
    if (this.state.currentCandleStickTechnicalIndicatorType !== technicalIndicatorType) {
      this.kLineChart.setCandleStickTechnicalIndicatorType(technicalIndicatorType)
      this.setState({
        currentCandleStickTechnicalIndicatorType: technicalIndicatorType
      })
    }
  }

  showTechnicalIndicator = () => {
    const isShowTechnicalIndicator = !this.state.isShowTechnicalIndicator
    if (isShowTechnicalIndicator) {
      this.technicalIndicatorTag = this.kLineChart.addTechnicalIndicator(this.state.currentTechnicalIndicatorType, 120)
    } else {
      this.kLineChart.removeTechnicalIndicator(this.technicalIndicatorTag)
    }
    this.setState({ isShowTechnicalIndicator }, () => {
      this.kLineChart.resize()
    })
  }

  screenShot = () => {
    const url = this.kLineChart.getConvertPictureUrl(true, true)
    const a = document.createElement('a')
    a.download = 'screenShot'
    a.href = url
    document.body.appendChild(a)
    a.click()
    a.remove()
  }

  onSettingClose = () => {
    const { timezone, technicalIndicatorParams } = this.state
    this.setState({
      tempTimezone: { ...timezone },
      tempTechnicalIndicatorParams: { ...technicalIndicatorParams },
      isShowSettingModal: false
    })
  }

  onSettingConfirm = () => {
    const { tempTimezone, tempTechnicalIndicatorParams } = this.state
    const timezone = { ...tempTimezone }
    const technicalIndicatorParams = { ...tempTechnicalIndicatorParams }
    this.kLineChart.setTimezone(timezone.key)
    for (const key in technicalIndicatorParams) {
      this.kLineChart.setTechnicalIndicatorParams(key, technicalIndicatorParams[key])
    }
    this.setState({
      timezone,
      technicalIndicatorParams,
      isShowSettingModal: false
    })
  }

  render () {
    const {
      theme, language,
      currentPeriod, currentCandleStickIcon,
      currentCandleStickTechnicalIndicatorType,
      currentTechnicalIndicatorType,
      isShowTechnicalIndicator,
      isShowSettingModal,
      tempTimezone,
      tempTechnicalIndicatorParams
    } = this.state
    return (
      <div className={`app-${theme}`}>
        {
          isShowSettingModal &&
          <SettingModal
            theme={theme}
            timezone={tempTimezone}
            technicalIndicatorParams={tempTechnicalIndicatorParams}
            onClose={this.onSettingClose}
            onConfirm={this.onSettingConfirm}
            onTimezoneChange={(timezone) => {
              this.setState({
                tempTimezone: timezone
              })
            }}
            onParamChange={(technicalIndicatorType, paramIndex, value) => {
              const params = { ...tempTechnicalIndicatorParams }
              params[technicalIndicatorType][paramIndex] = +(value || 1)
              this.setState({
                tempTechnicalIndicatorParams: params
              })
            }}/>
        }
        <i className="github-triangle-tag"/>
        <a
          className="github-icon icon-github"
          target="_blank"
          rel="noopener noreferrer"
          href="https://github.com/liihuu/KLineChart"/>
        <div className="period-container">
          {
            periods.map(period => {
              return (
                <span
                  key={period}
                  className={`${currentPeriod === period && 'period-selected'}`}
                  onClick={this.changePeriod(period)}>
                  {period}
                </span>
              )
            })
          }
          <i className="split-line" style={{ marginLeft: 8 }}/>
          <div className="select-container">
            <div className="select">
              <span>{currentCandleStickTechnicalIndicatorType}</span>
              <i className="select-arrow"/>
            </div>
            <ul className="select-drop-down-container">
              {
                ['MA', 'EMA', 'BOLL', 'SAR', 'NO'].map(type => {
                  return (
                    <li
                      key={type}
                      className="select-drop-down-row"
                      onClick={this.changeCandleStickTechnicalIndicatorType(type)}>
                      <span style={{ paddingLeft: 0 }}>{type}</span>
                    </li>
                  )
                })
              }
            </ul>
          </div>
          <i className="split-line"/>
          <div className="select-container">
            <div
              style={{ width: 30 }}
              className="select">
              <i className={`period-icon ${currentCandleStickIcon}`}/>
              <i className="select-arrow"/>
            </div>
            <ul className="select-drop-down-container">
              {
                candleStickStyles.map(candleStick => {
                  return (
                    <li
                      key={candleStick.key}
                      className="select-drop-down-row"
                      onClick={this.changeCandleStickStyle(candleStick)}>
                      <i className={`period-icon ${candleStick.icon}`}/>
                      <span>{intl(candleStick.text)}</span>
                    </li>
                  )
                })
              }
            </ul>
          </div>
          <i className="split-line"/>
          <i
            className={`period-icon icon-technical-indicator ${isShowTechnicalIndicator && 'period-icon-selected'}`}
            style={{ marginLeft: 12 }}
            onClick={this.showTechnicalIndicator}/>
          <i
            className="period-icon icon-setting"
            style={{ marginLeft: 12, fontSize: 16 }}
            onClick={() => {
              this.setState({
                isShowSettingModal: true
              })
            }}/>
          <i
            className="period-icon icon-screen-shot"
            style={{ marginLeft: 12 }}
            onClick={this.screenShot}/>

          <i
            className={`period-icon ${theme === 'dark' ? 'icon-sun' : 'icon-moon'}`}
            style={{ marginLeft: 12 }}
            onClick={() => {
              this.setState({
                theme: theme === 'dark' ? 'light' : 'dark'
              })
            }}/>
          <span
            style={{ fontSize: 14 }}
            onClick={() => {
              const lang = language === 'zh-CN' ? 'en' : 'zh-CN'
              setLanguage(lang)
              this.setState({
                language: lang
              })
            }}>
            {language === 'zh-CN' ? 'En' : ''}
          </span>
        </div>
        <div className="app-content">
          <div className="drawing-tools-container">
            {
              graphicMarks.map(graphicMark => {
                return (
                  <div
                    key={graphicMark.key}
                    className="drawing-tools-row"
                    onClick={() => { this.kLineChart.addGraphicMark(graphicMark.key) }}>
                    <i className={graphicMark.icon}/>
                  </div>
                )
              })
            }
            <div
              className="drawing-tools-row"
              onClick={() => { this.kLineChart.removeAllGraphicMark() }}>
              <i className="icon-delete"/>
            </div>
          </div>
          <div className="k-line-chart-container">
            <div id="k-line-chart" style={ !isShowTechnicalIndicator ? { height: '100%' } : {}}/>
            <div
              className="technical-indicator-container"
              style={{ display: isShowTechnicalIndicator ? 'flex' : 'none' }}>
              {
                technicalIndicatorTypes.map(technicalIndicatorType => {
                  return (
                    <span
                      key={technicalIndicatorType}
                      className={`${currentTechnicalIndicatorType === technicalIndicatorType && 'technical-indicator-selected'}`}
                      onClick={this.changeTechnicalIndicatorType(technicalIndicatorType)}>
                      {technicalIndicatorType}
                    </span>
                  )
                })
              }
            </div>
          </div>
        </div>
      </div>
    )
  }
}
