import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'

import { intl } from '../../intl/intl'

import './timezone.less'

const timezones = [
  { key: 'Etc/UTC', text: 'utc', utc: '' },
  { key: 'Pacific/Honolulu', text: 'honolulu', utc: '(UTC-10)' },
  { key: 'America/Juneau', text: 'juneau', utc: '(UTC-8)' },
  { key: 'America/Los_Angeles', text: 'los_angeles', utc: '(UTC-7)' },
  { key: 'America/Chicago', text: 'chicago', utc: '(UTC-5)' },
  { key: 'America/Toronto', text: 'toronto', utc: '(UTC-4)' },
  { key: 'America/Sao_Paulo', text: 'sao_paulo', utc: '(UTC-3)' },
  { key: 'Europe/London', text: 'london', utc: '(UTC+1)' },
  { key: 'Europe/Berlin', text: 'berlin', utc: '(UTC+2)' },
  { key: 'Asia/Bahrain', text: 'bahrain', utc: '(UTC+3)' },
  { key: 'Asia/Dubai', text: 'dubai', utc: '(UTC+4)' },
  { key: 'Asia/Ashkhabad', text: 'ashkhabad', utc: '(UTC+5)' },
  { key: 'Asia/Almaty', text: 'almaty', utc: '(UTC+6)' },
  { key: 'Asia/Bangkok', text: 'bangkok', utc: '(UTC+7)' },
  { key: 'Asia/Shanghai', text: 'shanghai', utc: '(UTC+8)' },
  { key: 'Asia/Tokyo', text: 'tokyo', utc: '(UTC+9)' },
  { key: 'Australia/Sydney', text: 'sydney', utc: '(UTC+10)' },
  { key: 'Pacific/Norfolk', text: 'norfolk', utc: '(UTC+12)' }
]

export default class Timezone extends PureComponent {
  static defaultProps = {
    theme: 'dark',
    timezone: {},
    onTimezoneChange: () => {}
  }

  static propTypes = {
    theme: PropTypes.string,
    language: PropTypes.string,
    timezone: PropTypes.object,
    onTimezoneChange: PropTypes.func
  }

  render () {
    const { timezone, theme } = this.props
    return (
      <div
        className={`timezone-${theme}`}>
        <div className="timezone-content">
          <span className="timezone-content-title">{intl('timezone')}</span>
          <div className="timezone-content-select">
            <span>{timezone.utc}&nbsp;{intl(timezone.text)}</span>
            <i className="timezone-content-select-arrow"/>
            <ul className="timezone-content-select-drop-down-container">
              {
                timezones.map(timezone => {
                  return (
                    <li
                      key={timezone.key}
                      onClick={() => { this.props.onTimezoneChange(timezone) }}>
                      {timezone.utc}&nbsp;{intl(timezone.text)}
                    </li>
                  )
                })
              }
            </ul>
          </div>
        </div>
      </div>
    )
  }
}
