import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'

import './technical-indicator-params.less'

const technicalIndicatorParamCounts = {
  MA: 4,
  EMA: 3,
  VOL: 3,
  MACD: 3,
  BOLL: 1,
  KDJ: 3,
  RSI: 3,
  BIAS: 3,
  BRAR: 1,
  CCI: 1,
  DMI: 2,
  CR: 5,
  PSY: 1,
  DMA: 3,
  TRIX: 2,
  OBV: 1,
  VR: 2,
  WR: 3,
  MTM: 2,
  EMV: 2,
  SAR: 3
}

export default class TechnicalIndicatorParams extends PureComponent {
  static propTypes = {
    theme: PropTypes.string,
    language: PropTypes.string,
    technicalIndicatorParams: PropTypes.object,
    onParamChange: PropTypes.func
  }

  static defaultProps = {
    theme: 'dark',
    technicalIndicatorParams: {},
    onParamChange: () => {}
  }

  renderRows (technicalIndicatorTypes) {
    const { technicalIndicatorParams, onParamChange } = this.props
    return technicalIndicatorTypes.map(technicalIndicatorType => {
      const inputCount = technicalIndicatorParamCounts[technicalIndicatorType]
      const inputDoms = []
      const technicalIndicatorParam = technicalIndicatorParams[technicalIndicatorType] || []
      for (let i = 0; i < inputCount; i++) {
        inputDoms.push(
          <input
            key={`input_${i}`}
            className="technical-indicator-params-column-row-input"
            value={technicalIndicatorParam[i] || ''}
            onChange={(e) => {
              const value = e.target.value
              if (/^[1-9]*$/.test(value)) {
                onParamChange(technicalIndicatorType, i, value)
              }
            }}/>
        )
      }
      return (
        <li key={technicalIndicatorType}>
          <span
            className="technical-indicator-params-column-row-title">
            {technicalIndicatorType}
          </span>
          {inputDoms}
        </li>
      )
    })
  }

  render () {
    return (
      <div
        className={`technical-indicator-params-${this.props.theme}`}>
        <ul
          className="technical-indicator-params-column">
          {
            this.renderRows([
              'MA', 'EMA', 'VOL', 'MACD', 'BOLL', 'KDJ', 'RSI', 'BIAS', 'BRAR', 'CCI', 'DMI'
            ])
          }
        </ul>
        <ul
          className="technical-indicator-params-column">
          {
            this.renderRows([
              'CR', 'PSY', 'DMA', 'TRIX', 'OBV', 'VR', 'WR', 'MTM', 'EMV', 'SAR'
            ])
          }
        </ul>
      </div>
    )
  }
}
