import React, { PureComponent, createRef } from 'react'
import PropTypes from 'prop-types'

import { intl } from '../intl/intl'

import Timezone from './timezone/Timezone'
import TechnicalIndicatorParams from './technicalindicatorparams/TechnicalIndicatorParams'

import './setting-modal.less'

const tabs = [
  { key: 'timezone', text: 'timezone' },
  { key: 'technical_indicator_params', text: 'technical_indicator_params' }
]

export default class SettingModal extends PureComponent {
  static propTypes = {
    theme: PropTypes.string,
    timezone: PropTypes.object,
    technicalIndicatorParams: PropTypes.object,
    onClose: PropTypes.func,
    onConfirm: PropTypes.func,
    onTimezoneChange: PropTypes.func,
    onParamChange: PropTypes.func
  }

  static defaultProps = {
    theme: 'dark',
    timezone: {},
    technicalIndicatorParams: {},
    onTimezoneChange: () => {},
    onClose: () => {},
    onConfirm: () => {},
    onParamChange: () => {}
  }

  state = {
    currentTab: 'timezone'
  }

  rootEl = createRef()

  componentDidMount () {
    setTimeout(() => {
      this.rootEl.current.className = `setting-modal-${this.props.theme} setting-modal-show`
    })
  }

  render () {
    const { currentTab } = this.state
    const {
      theme, timezone,
      technicalIndicatorParams, onClose,
      onConfirm, onTimezoneChange, onParamChange
    } = this.props
    return (
      <div
        ref={this.rootEl}
        className={`setting-modal-${theme}`}>
        <div className="setting-modal-title-container">
          <div className="setting-modal-title-tab-container">
            {
              tabs.map(tab => {
                return (
                  <span
                    key={tab.key}
                    className={`setting-modal-title-tab ${currentTab === tab.key && 'setting-modal-title-tab-selected'}`}
                    onClick={() => { this.setState({ currentTab: tab.key }) }}>
                    {intl(tab.text)}
                  </span>
                )
              })
            }
          </div>
          <i
            className="icon-close setting-modal-title-icon"
            onClick={onClose}/>
        </div>
        <div
          className="setting-modal-content-container">
          {
            currentTab === 'timezone' &&
            <Timezone
              theme={theme}
              timezone={timezone}
              onTimezoneChange={onTimezoneChange}/>
          }
          {
            currentTab === 'technical_indicator_params' &&
            <TechnicalIndicatorParams
              theme={theme}
              technicalIndicatorParams={technicalIndicatorParams}
              onParamChange={onParamChange}/>
          }
        </div>
        <div className="setting-modal-bottom-container">
          <button
            datatype="cancel"
            onClick={onClose}>
            {intl('cancel')}
          </button>
          <button
            datatype="confirm"
            onClick={onConfirm}>
            {intl('confirm')}
          </button>
        </div>
      </div>
    )
  }
}
